@import url("https://rsms.me/inter/inter.css");

html,
body {
  margin: 0;
  padding: 0;
  background: #272730;
  overflow: hidden;
}

body {
  font-family: "Inter var", sans-serif;
  font-size: 14px;
  color: white;
  background: #f0f0f0;
}

#root {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

canvas {
  position: absolute;
}

.content {
  padding-top: 10px;
  transform: translate3d(calc(50% + 40px), 0, 0);
  text-align: left;
  background: #202035;
  color: white;
  padding: 10px 15px;
  border-radius: 5px;
  top: -35px;
  left: -40px;
  position: relative;
  width: 100px;
  display: none;
  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10 and IE 11 */
  user-select: none; /* Standard syntax */
  
}
.content.active {
  display: block;
  cursor: pointer;
}

.content::before {
  content: "";
  position: absolute;
  top: 25px;
  left: -40px;
  height: 1px;
  width: 40px;
  background: black;
}

.sector {
  padding-top: 10px;
  font-size: 12px;
  font-style: italic;
}
.name {
  text-transform: capitalize;
}
